    
    import './main.css'

    export const Main = ({lang}) =>{
    return(

  

        <div className="main">
      <div className="main__details">
          <div className="wrapInfo">
          <p className="detail main__item">
              {lang==="ru" ? `НЕМНОГО ОБО МНЕ` : 'SOME DETAILS ABOUT ME'}
          </p>
          <h1 className="aboutAnn main__item">
              {lang==="ru" ? `Про Анну Валеску` : 'About Anna Valeska'}
          </h1>
          </div>
          <div className="instagram_wrap main__item">
          <a href="https://instagram.com/redbeastanna?igshid=1jlps1y5k9xd7" className="main__instagram" >

              {lang==="ru" ? `Подпишись на Instagram` : 'Subscibe me Instagram'}
              <span className="instagram__iconlink">&#8594;</span>
          </a>
          </div>
      </div>

        </div>
    

    );
    }
