import emailJs from "emailjs-com";
import './contact.css';
import {useState} from "react";
import {Button,  TextField} from "@material-ui/core";



const Contact = ({lang}) =>{

    const [nameUser,setNameUser] = useState('');
    const [email,setEmail] = useState('');
    const [message,setMessage]=useState('')

    const [checkEmail,setCheckEmail] =useState(false);

    const [validName,setValidName] = useState(null)
    const [validEmail,setValidEmail] = useState(null)
    const [validMessege,setValidMessege] = useState(null)

    const [form,setForm] = useState(null);
    const [color,setColor] = useState(null);


    const sendEmail=(e)=> {
        e.preventDefault();

        if (nameUser.length>4 && message.length>15 && checkEmail) {

            emailJs.sendForm('service_8zy77vd', 'template_sv8z71j', e.target,
                'user_xfg6Lq0YEVmdSv9KquWrU')
                .then((result) => {
                    console.log(result.text);
                }, (error) => {
                    console.log(error.text);
                });

            setForm("Sent");
            setColor('validForm');
            setNameUser('');
            setEmail('');
            setMessage('');

            e.target.reset();
        }else {
            setColor('notValidForm');
            setForm(lang==="ru" ? `Заполните пожалуйста коректно` : `Please write correct`);
        }
    }

const handleNameChange = (e) =>{

switch (e.target.name){
    case "name" :{
        setNameUser(e.target.value)

        if (nameUser.length<3){
            setValidName('Write please correct your name must be more than 3 letters')
        }else{
            setValidName('')
        }
    };break;
    case "email" :{
        setEmail(e.target.value);
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if(!re.test(String(email).toLowerCase())){
            setValidEmail('Write please correct your email')
            setCheckEmail(false);
        }else{
            setValidEmail('')
            setCheckEmail(true);
        }
    }break;
    case "messege" :{
        setMessage(e.target.value);
        if (message.length<15){
            setValidMessege('Write please correct your message must be more than 15 letters')
        }
        else {
            setValidMessege('')
        }
    }break;
}

}

    return(

        <div className="contactWrap">
            <h2>
                {lang==="ru" ? `Давай поговрим` : `Let's talk`}

            </h2>
            <form className="contact-form" onSubmit={sendEmail}>
                <span className="notValidForm">{validName}</span>
                <TextField
                    label={lang==="ru" ? `Имя` : `Name`}
                    multiline
                    rowsMax={10}
                    rosMin={1}
                    name="name"
                    className="form__item"
                    value={nameUser}
                    onChange={handleNameChange}
                />
                <span className="notValidForm">{validEmail}</span>
                <TextField
                    label="Email"
                    multiline
                    rowsMax={10}
                    name="email"
                    className="form__item"
                    value={email}
                    onChange={handleNameChange}
                />
                <h1>asd</h1>
                <span className="notValidForm">{validMessege}</span>
                <TextField
                    id="standard-textarea"
                    label={lang==="ru" ? `Сообщение` : `Message`}
                    multiline
                    name="messege"
                    className="form__item"
                    value={message}
                    onChange={handleNameChange}
                />
                <div>
                    <p className={color}>{form}</p>
                </div>
                <Button variant="outlined" value="Send" className="sendMessage" type="submit">
                    {lang==="ru" ? `Отправить` : `Send`}
                </Button>
            </form>
        </div>
    );
}
export default Contact;