import {useState} from "react";
import {CardImageWork} from "../cardImageWork";
import artsOne from "../imagesWork/artsOne.jpg";
import artsTwo from "../imagesWork/artsTwo.jpg";
import artsThree from "../imagesWork/artsThree.jpg"
import artsFour from "../imagesWork/artsFour.jpg"


const Arts = ({lang}) =>{

    return(

        <div className="wrapWorkComponents">

<h2>

    {lang==="ru" ? `Картины` : 'Arts'}
</h2>
            <hr/>
            <div className="itemWrap">
            <CardImageWork allImg={artsOne}/>
            <CardImageWork allImg={artsTwo}/>
            <CardImageWork allImg={artsThree}/>
            <CardImageWork allImg={artsFour}/>
            </div>
        </div>
    )
}
export default Arts;
