import React from "react";
import {BrowserRouter, NavLink} from "react-router-dom";
import {Route, Switch} from "react-router";
import Arts from "./componetsWork/arts/arts";
import Dresses from "./componetsWork/dresses/dresses";
import "./work.css"
import {useState} from "react";
import NotFound from "../notFound/notFound";



const Work = ({lang}) => {
    const [image,setImage] = useState('arts');

    return(
        <div className='works'>


            <BrowserRouter>


                <div className= {image ==="arts" ? `wrapLink` : 'wrapLinkTwo'}  >
                    <div className='wrap__item'>
                        <NavLink to="/work/arts"  className="link__item" exact onClick={()=>{setImage("arts")}} activeClassName="active" >{lang==="ru" ? `Картины` : 'Arts'}</NavLink>
                        <NavLink to="/work/dresses" className="link__item" exact onClick={()=>{setImage("dresses")}} activeClassName="active" >{lang==="ru" ? `Одежда` : 'Clothes'}</NavLink>
                    </div>

                </div>





                <div className='works__picture'>
                    <Switch>
                        <Route path='/work/' render={()=><Arts lang={lang}/>} exact />
                        <Route path='/work/arts' render={()=><Arts lang={lang}/>}/>
                        <Route path='/work/dresses' render={()=><Dresses lang={lang}/>}/>

                        <Route children={()=><NotFound/>} />
                    </Switch>
                </div>

            </BrowserRouter>
        </div>
    )

}
export default Work;