import { Button } from '@material-ui/core'

import { NavLink} from 'react-router-dom'
import './nav.css'

const Nav = ({lang,setLang})=>{


    return(
        
        <nav className="nav">
         
         <NavLink to="/main" className="links" activeClassName="selected" >
         {lang==="ru" ? `Обо мне` : 'About me'}
      </NavLink>
      <NavLink to="/work" className="links" activeClassName="selected">
        {lang==="ru" ? `Мои роботы` : 'My work'}
      </NavLink>
      <NavLink to="/project" className="links" activeClassName="selected">
        {lang==="ru" ? `Проекты` : `Projects`}
      </NavLink>
      <NavLink to="/contact" className="links" activeClassName="selected">
        {lang==="ru" ? `Контакты` : `Contact`}
      </NavLink>
      <Button variant="outlined" className="nav__language" onClick={()=> lang==="en" ? setLang("ru") : setLang("en")} >
      {/* ПЕредавать из апп сетЛанг и менять ланг */}
        {lang==="ru" ? `Изменить язык  ` : `Change language  `}
        {lang}
        </Button>

         <div className="phoneChangeLang" onClick={()=> lang==="en" ? setLang("ru") : setLang("en")}>
             <p>{lang.toUpperCase()}</p>
         </div>

        </nav>
    );
}
export default Nav;